body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  min-height: 100vh;
  text-align: center;
  background-color: #282c34;
  color: aliceblue;
  padding: 20px;
}

.App-header {
  margin-bottom: 50px;
}

.App-description {
  margin-bottom: 50px;
  color: #56a4a4;
}

.App-link {
  color: #61dafb;
  text-decoration: none;
}

form {
  max-width: 70%;
  margin: 20px auto;
  padding: 20px;
  border: 2px solid aliceblue;
}

label {
  display: block;
}

input {
  margin-left: 20px;
  margin-top: 20px;
}

input[type="checkbox"] {
  margin-left: 0;
  margin-right: 10px;
}

button {
  max-width: 200px;
  padding: 10px 20px;
  margin: 20px 0;
}

span {
  color: red;
  font-size: 12px;
  display: block;
}

